import styles from './Home.module.css'

import Searched from './add-ons/Searched'

function Home() {
    return (
        <section className={styles.container}>
            <div className={styles.content}>
                <div className={styles.banner}>
                    <p>
                        Na <span>Marcos Veculos</span>, seu novo carro te espera! <span>Preo justo</span>, <span>qualidade</span> e <span>confiana</span>. Vem fazer um timo negcio!
                    </p>
                </div>
                <Searched />
            </div>
        </section>
    )
}

export default Home