import styles from './Help.module.css'

import FAQ from '../../../img/FAQimg.png'

function Help() {

    return (
        <section className={styles.container}>
            <div className={styles.content}>
                <div className={styles.banner}>
                    <div className={styles.banner_left}>
                        <h1>Bem-vindo a central de ajuda <span>Marcos Veculos</span></h1>
                        <p>
                            Tire todas as suas dvidas ou acione <strong>nossos canais de atendimento</strong>
                        </p>
                    </div>
                    <div className={styles.banner_right}>
                        <img src={FAQ} alt='FAQ AJUDA'/>
                    </div>
                </div>
                <input type="text" placeholder="Digite sua dvida"></input>

                <div className={styles.doubts}>
                    <h3>
                        Dvidas frequentes
                    </h3>
                    <ol>
                        <li>
                            <a href=''>
                                Quais dados consigo alterar no meu cadastro?
                            </a>
                        </li>
                        <li>
                            <a href=''>
                                Onde fao essas alteraes?
                            </a>
                        </li>
                        <li>
                            <a href=''>
                                Cancelamento de cadastro
                            </a>
                        </li>
                        <li>
                            <a href=''>
                                Quais dados cadastrai NO posso alterar?
                            </a>
                        </li>
                        <li>
                            <a href=''>
                                Quais palavras ou caracteres no podem ser inseridos no campo de observao?
                            </a>
                        </li>
                        <li>
                            <a href=''>
                                Porque o campo de observao do meu anncio no aparece?
                            </a>
                        </li>
                        <li>
                            <a href=''>
                                O que  Comparador?
                            </a>
                        </li>
                        <li>
                            <a href=''>
                                O que  Cardelivery?
                            </a>
                        </li>
                        <li>
                            <a href=''>
                                Quem vai levar o Carro at minha casa?
                            </a>
                        </li>
                        <li>
                            <a href=''>
                                Termos de Uso e Poltica de Privacidade
                            </a>
                        </li>
                    </ol>
                </div>
            </div>
        </section>
    )
}

export default Help